<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">预约签到</block>
		</cu-custom>
		<view class="butgroup">
			<button class="margin-top" type="default" @click="getCurrentLocation()">获取当前位置</button>
			<!-- <button class="margin-top" type="primary" @click="getaddress()">获取选择的位置</button> -->
		</view>

		<view class="margin-top" style="color: red;text-align: center;">
			<!-- {{positionInfo.address}}, -->
			{{positionInfo.longitude}},{{positionInfo.latitude}}
		</view>

		<view class="margin-top submitbut butgroup">
			<button class="margin-top" type="primary" @click="submit">签到</button>
			<!-- <button class="cu-btn block bg-gradual-green margin-tb-sm lg" @click="submit">签到</button> -->
		</view>
	</view>
</template>

<script>
	import configData from '@/config/index.js'
	const jinweidurand = configData.jinweidurand
	export default {
		onLoad(options) {
			this.msgstr = options.msgstr
		},
		mounted() {
			this.userinfo = uni.getStorageSync('userinfo')
		},
		data() {
			return {
				msgstr: "",
				userinfo: {},
				positionInfo: {
					address: '',
					longitude: '', //经度
					latitude: '', //纬度
				},

			}
		},
		methods: {
			// 通过自带的方法获取到当前的经纬度，调用方法获取到地址获取到地址的中文信息
			getCurrentLocation() {
				let that = this //在uniapp中药定义一下this才能使用
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log(res)
						that.positionInfo.longitude = res.longitude;
						that.positionInfo.latitude = res.latitude;
						that.loAcquire(that.positionInfo.longitude, that.positionInfo.latitude)
					}
				});
			},
			// 获取当前地址
			loAcquire(longitude, latitude) {
				let that = this;
				uni.showLoading({
					title: '加载中',
					mask: true
				});
				let str = `output=jsonp&key='ZYCBZ-6LNEX-54K4P-TITG3-GPFD5-5EBVP'=${latitude},${longitude}` //记得在这里要输入密钥哦！
				uni.request('https://apis.map.qq.com/ws/geocoder/v1/?' + str, {}).then(res => {
					console.log(res);
					uni.hideLoading();
					if (res.status == 0) {
						that.positionInfo.address = '当前位置是:' + res.result.address_component.street_number; //当前定位
					}
				})
			},
			// 获取选择地址
			getaddress() {
				let that = this
				uni.chooseLocation({
					success: function(res) {
						console.log(res)
						that.positionInfo.address = '选择的位置是：' + res.name
						that.positionInfo.longitude = '经度：' + res.longitude
						that.positionInfo.latitude = '纬度：' + res.latitude
					}
				});
			},
			getnowdate() {
				var nowDate = new Date();
				var year = nowDate.getFullYear();
				var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1) : nowDate.getMonth() + 1;
				var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
				var dateStr = year + "-" + month + "-" + day;
				return dateStr;
			},

			getnowtime() {
				let date = new Date()
				let hours = date.getHours()
				let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
				let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
				let time = hours + ':' + minutes + ':' + seconds
				return time

			},
			async submit() {
				let that = this
				console.log(jinweidurand)
				// jinweidurand
				// jinweidurand:{
				// 	latitude: [22.00719,22.00779],
				// 	longitude: [100.79709,100.79769]
				// },
				//{{positionInfo.longitude}},{{positionInfo.latitude}},
				let longitudemin = jinweidurand.longitude[0]
				let longitudemax = jinweidurand.longitude[1]
				let latitudemin = jinweidurand.latitude[0]
				let latitudemax = jinweidurand.latitude[1]
				let longitude = this.positionInfo.longitude
				let latitude = this.positionInfo.latitude
				if (longitudemin <= longitude && longitude <= longitudemax && latitudemin <= latitude && latitude <=
					latitudemax) {

					let msgobj = JSON.parse(this.msgstr)
					msgobj.datetime = this.getnowdate() + " " + this.getnowtime()
					let resdata = await new this.Request(this.Urls.m().updatayuyuestatus, msgobj).modepost()
					if (resdata == 'success') {
						uni.showToast({
							title: "预约签到成功",
							icon: 'success',
							//显示持续时间为 2秒
							duration: 2000
						})
					}

				} else {
					uni.showToast({
						title: '不在打卡范围内',
						icon: 'none',
						duration: 2000
					});
				}









			}
		}
	}
</script>

<style scoped>
	.butgroup {
		margin-top: 5%;
		padding-left: 3%;
		padding-right: 3%;
	}
</style>